import React, { Component } from 'react';
//My components
import HomeScreen from './screens/HomeScreen';
import LoginScreen from './screens/LoginScreen';
import AnswerScreen from './screens/AnswerScreen';
import ResultScreen from './screens/ResultScreen';
import NewQuestionScreen from './screens/NewQuestionScreen';
import LeaderBoardScreen from './screens/LeaderBoardScreen';
import Navbar from './components/Navbar';
import Footer from './components/Footer';
//Actions
import { receiveData } from './actions/shared';
//Libraries
import { connect } from 'react-redux';
import LoadingBar from 'react-redux-loading-bar';
import { Route, Redirect } from 'react-router-dom';

class App extends Component {
  componentDidMount() {
    this.props.dispatch(receiveData());
  }

  render() {
    const { authed } = this.props;
    return (
      <React.Fragment>
        <Navbar />
        <LoadingBar />
        {!authed && <Redirect to="/login" />}
        <main>
          <Route path="/" exact component={HomeScreen} />
          <Route path="/login" component={LoginScreen} />
          <Route path="/answer/:id" component={AnswerScreen} />
          <Route path="/result/:id" component={ResultScreen} />
          <Route path="/leaderboard" component={LeaderBoardScreen} />
          <Route path="/new" component={NewQuestionScreen} />
        </main>
        <Footer />
      </React.Fragment>
    );
  }
}

export default connect(({ authed }) => ({ authed }))(App);
